#map{ 
    position: absolute; 
    top:0; 
    bottom:0; 
    right:0; 
    left:0; 
    z-index:2;
}
.controls{
    position:absolute; 
    bottom:0; 
    right:0; 
    padding:10px; 
    z-index:10; 
}

/*style do wlasciwej mapy*/

.ac_poi_map{
    min-height:400px;
}
.entry-content a,
.entry-content a:hover,
.ac_leaflet_map .entry-content a,
.ac_leaflet_map img{
    box-shadow: none!important;
}